@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho+B1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
:root {
  --brown: #192841;
  --blue: #a4c1c9;
  --tan: #294068;
  --light-green: #85865f;
  --green: #4f583d;
  --dark-gray: #191d14;
  --black: #0f110c;
  --white: #ffffff;
  --transparent: #00000000;
  --transparent-gray: #16181faa;
  --transparent-black: #06080faa;
  --transparent-white: #ffffffaa;
  --transparent-red: #4f161644;
  --navy-blue: #294068;

  /* --ff-written: 'Indie Flower', cursive; */
  --ff-written: 'Bangers', cursive;
  --ff-main: 'Shippori Mincho B1', serif;
  /* --ff-main: 'Lobster', cursive; */

  /* --font-size: clamp(14px, 1vw, 18px); */
  --font-size: 1rem;

  --shadow: 0 0 .625rem var(--blue);

  --frame: min(900px, 84%);
}
:is(html, body, #root) {
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  position: absolute;
  width: 100vw;
  min-height: 100vh;
}
*, *::after, *::before {
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#root {
  padding: 0.825rem 0 5rem 0;
  background: var(--tan);
}
main {
  margin-top: 2.25rem;
}
img {
  width: 100%;
}
img.invert {
  -webkit-filter: invert(1);
          filter: invert(1);
}
div.circle-image-container {
  display: grid;
  padding: 0;
  -webkit-align-items: center;
          align-items: center;
  width: min(15ch, 200px);
  height: min(15ch, 200px);
  border-radius: 50%;
  border: 1px solid var(--brown);
  box-shadow: 0 0 5px var(--brown);
  margin: 1rem auto;
  overflow: hidden;
}
div.circle-image-container > img {
  min-width: 100%;
  min-height: 100%;
}
:is(input, p, span, button, div, h1, h2, h3, h4, h5, h6, textarea) {
  margin: 0;
  padding: 0.125rem 0.25rem;
  color: var(--blue);
  font-size: 1rem;
  line-height: 2.25rem;
  font-family: var(--ff-main);
}
input, textarea, button {
  color: var(--black);
}
textarea {
  resize: none;
  overflow: hidden;
}
:is(h1, h2, h3, h4, h5, h6, b) {
  letter-spacing: 1.3px;
  font-family: var(--ff-written);
}

h1 {
  letter-spacing: 4px;
  color: var(--black);
  font-size: 3rem;
  line-height: 4.5rem;
}
h2 {
  font-size: 2rem;
  line-height: 3rem;
}
h3 {
  font-size: 1.8rem;
  line-height: 2.7rem;
}
h4 {
  font-size: 1.6rem;
  line-height: 2.4rem;
}
h5 {
  font-size: 1.4rem;
  line-height: 2.1rem;
}
h6, b {
  font-size: 1.2rem;
  line-height: 1.8rem;
}

@media (max-width: 1099px) {
  :is(h1, h2, h3, h4, h5, h6):not(.short) {
    display: none;
  }
}
@media (min-width: 1100px) {
  :is(h1, h2, h3, h4, h5, h6):is(.short) {
    display: none;
  }
}

.text-center { 
  text-align: center;
}
@-webkit-keyframes actor-rotate {
  50% {
    -webkit-transform: scale(0.97) rotate(3deg);
            transform: scale(0.97) rotate(3deg);
  }
}
@keyframes actor-rotate {
  50% {
    -webkit-transform: scale(0.97) rotate(3deg);
            transform: scale(0.97) rotate(3deg);
  }
}
@-webkit-keyframes character-rotate {
  50% {
    -webkit-transform: scale(0.97) rotate(3deg);
            transform: scale(0.97) rotate(3deg);
  }
}
@keyframes character-rotate {
  50% {
    -webkit-transform: scale(0.97) rotate(3deg);
            transform: scale(0.97) rotate(3deg);
  }
}

.character-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  background-color: var(--black);
  box-shadow: var(--shadow);
  padding: var(--font-size);
  margin: calc(var(--font-size) * 2) auto;
  width: var(--frame);
  -webkit-animation: character-rotate 0.2s;
          animation: character-rotate 0.2s;
}
.character-card > div.character-played-by {
  grid-column: 1 / 3;
  width: 80%;
}
.character-card > div.character-played-by > header {
  width: 100%;
  display: grid;
  grid-template-columns: 2rem 1fr 2rem;
  text-align: center;
}
.character-card > div.character-played-by > header > h3 {
  grid-column: 2;
}
.character-card > div.character-info {
  width: min(34vw, 275px);
  margin: auto;
}
.character-card > div.character-info:first-child:last-child {
  grid-column: 1 / 3;
  text-align: center;
}
div.character-card > div.character-image {
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
  width: min(34vw, 225px);
  height: min(34vw, 225px);
  border-radius: 50%;
  box-shadow: var(--shadow);
  margin: var(--font-size) auto;
  overflow: hidden;
}
div.character-card > div.character-image > img {
  min-width: 100%;
  min-height: 100%;
}
@media (max-width: 800px) {
  .character-card {
    grid-template-columns: 1fr;
  }
  .character-card > div {
    grid-column: 1;
  }
  .character-card > div.character-info {
    text-align: center;
    margin-bottom: 1rem;
  }
}
.character-card.actor {
  -webkit-animation: actor-rotate 0.2s;
          animation: actor-rotate 0.2s;
}
.form {
  width: var(--frame);
  box-shadow: var(--shadow);
  background-color: var(--black);
  margin: 0 auto;
  padding: 1ch;
}
.form > header {
  text-align: center;
  padding-bottom: var(--font-size);
  margin-bottom: calc(var(--font-size) * 1.5);
  border-bottom: 1px solid var(--tan);
}
.form > section {
  margin-bottom: calc(var(--font-size) * 2);
}
.form > section > div.input-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 1rem 0;
}

/* Style for About.js */
.about-container > div.about {
  width: var(--frame);
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  margin: auto;
  box-shadow: var(--shadow);
  background-color: var(--black);
  padding: 1rem;
}
.about-container > div.about > section.about-input {
  width: 94%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.about-container > div.about > section.about-input > :is(input, textarea) {
  color: var(--black);
}
.about-container > div.about > section.about-input > button {
  color: var(--black);
  grid-column: 1 / 3;
  width: 94%;
  margin: 2rem auto;
}
.about h1 {
  color: var(--white);
}
.about > .about-section > p.about-text {
  max-width: 40ch;
  padding: 1.5rem 0;
}
.about > .about-section > p.about-text:not(:last-child) {
  border-bottom: 1px dashed var(--blue);
}
.about .string-input-array section {
  display: grid;
  margin: 1rem 0;
  grid-template-columns: 1fr;
}
nav#nav {
  position: fixed;
  left: 1rem;
  top: 2rem;
  width: 2rem;
  height: 2rem;
  transition: 0.5s;
}
nav#nav + div#nav-overlay {
  display: grid;
  background-color: var(--transparent-red);
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  opacity: 0;
  grid-template-columns: 1fr -webkit-min-content 1fr;
  grid-template-columns: 1fr min-content 1fr;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: 0.5s;
}
nav#nav + div#nav-overlay > ul {
  box-shadow: var(--shadow);
  display: grid;
  grid-column: 2;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  width: min(35ch, 94vw);
  height: 75vh;
  padding: 0;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  background-color: var(--black);
  list-style-type: none;
}
nav#nav + div#nav-overlay > ul > li {
  font-family: var(--ff-written);
  width: calc(35ch / 1.8);
  font-size: 1.8rem;
  line-height: 3.6rem;
  display: block;
  margin: 0;
  padding: 0;
  transition: 0.2s;
}
nav#nav + div#nav-overlay > ul > li:hover {
  background-color: var(--transparent-white);
  color: var(--black);
}
nav#nav.open {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
nav#nav.open + div#nav-overlay {
  z-index: 10;
  opacity: 1;
}
footer#audio-player {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 4rem;
  width: 100vw;
  background-color: var(--white);
  box-shadow: 0 -1px 3px var(--transparent-gray);
}

footer#audio-player > section.audio-player-container {
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 1fr 3.5rem;
  grid-template-rows: 3rem 0.5rem;
  margin: 0.5rem 2rem 0 2rem; 
  padding: 0;
  height: 3.5rem;
  color: var(--black);
}

footer#audio-player > section.audio-player-container > section.audio-player-image {
  grid-column: 3;
  grid-row: 1 / 3;
  height: 3.5rem;
  width: 3.5rem;
  -webkit-transform: translateY(-0.25rem);
          transform: translateY(-0.25rem);
  overflow: hidden;
}
footer#audio-player > section.audio-player-container > section.audio-player-image > img {
  min-width: 3.5rem;
  min-height: 3.5rem;
}
footer#audio-player > section.audio-player-container > section.progress {
  grid-column: 1 / 3;
  border-top: 0.5rem solid var(--tan);
  height: 0.5rem;
}


